@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
@font-face {
  font-family: Roboto;
  src: url(/static/media/Roboto-Medium.7429a63c.ttf);
}

@keyframes appear {
  0% {
    opacity: 0;
  }
}

@keyframes fade {
  100% {
    opacity: 0;
    transform: translateX(15px);
  }
}

/* base styles */
* {
  margin: 0;
  font-family: Roboto, system-ui, "Quicksand" ;
  color: #333;
}

body {
  background: #f7f7f7;
}

.content {
  /* max-width: 800px;
  margin: auto; */
  padding: 20px;
}

input, textarea {
  font-size: 1rem;
  font-family: system-ui, Robot, "Quicksand" ;
}
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.8rem 2rem;
  border-bottom: 1px solid #dbdbdb;
  background: white;
}

.navbar .logo {
  font-size: 2rem;
  color: #f1356d;
  font-weight: bold;
  transition: transform 1s ease-in-out;
}

.navbar .logo:hover {
  transform: rotateY(360deg);
}

.navbar a {
  text-decoration: none;
  padding: 0.5rem;
}

.navbar-left {
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
  align-items: center;
}

.sign a:hover {
  color: #f1356d;
}

.signup {
  color: white;
  margin-left: 0.5rem;
  background: #f1356d;
  border-radius: 8px;
}

.signup:hover {
  color: white !important;
  background: hsl(342, 87%, 70%);
}
@font-face {
  font-family: UbuntuMono;
  src: url(/static/media/UbuntuMono-Regular.9fa7d55c.ttf);
}

@keyframes cursor-fade {
  100% {
    opacity: 0;
  }
}

.home {
  font-family: UbuntuMono;
  height: 75vh;
  padding-top: 2rem;
  padding-left: 1rem;
  background: bottom right no-repeat url(/static/media/undraw_hello_re_3evm.04beb8e4.svg);
  background-size: auto 100%;
  animation: appear 1s ease-out;
}

.home h1 {
  font-size: 4rem;
  margin-bottom: 1rem;
}

.intro {
  font-size: 2rem;
  margin-bottom: 3rem;
}

.intro * {
  color: #999999;
}

.typewriter {
  color: black;
  width: 40%;
  font-size: 2rem;
  font-family: UbuntuMono, 'Courier New', Courier, monospace;
  animation: appear 1s ease-out backwards;
}

.cursor {
  color: rgb(185, 185, 185);
  font-size: 2rem;
  animation: cursor-fade 500ms ease-in-out alternate infinite;
}

.quote {
  width: 45%;
}

.blogs {
  max-width: 600px;
  margin: auto;
}

.blog-preview {
  color: #707070;
  margin-top: 2rem;
  padding: 1rem 1rem;
  border: 1px solid #f0f0f0;
  border-radius: 5px;
  transition: all 300ms;
  background: #fafafa;
}

.blog-preview:hover {
  box-shadow: 0px 5px 5px #949494;
  transform: translateY(-5px);
}

.blog__title {
  font-size: 1.6rem;
  margin-bottom: 1rem;
  font-weight: normal;
  color: #808080;
}

.blog__author {
  display: flex;
  justify-content: space-between;
}

.blog__title:hover, .blog__author:hover {
  cursor: default;
}

.likes * {
  color: rgb(194, 194, 194);
  font-size: 0.8rem;
}


@keyframes fade {
  0% {
    opacity: 0.3;
  }
}

.loading {
  margin-top: 1rem;
  font-size: 2rem;
  animation: fade 500ms ease-in-out infinite alternate;
}

.loading span {
  margin-right: 1rem;
}

.create {
  max-width: 750px;
  margin: auto;
}

.create h1 {
  font-weight: normal;
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.create p {
  margin-bottom: 1rem;
}

.create input {
  margin-top: 0.5rem;
  padding-left: 0.5rem;
  height: 1.5rem;
}

.create input::placeholder {
  opacity: 0.5;
}

.create textarea {
  margin-top: 0.5rem;
}

.create form {
  padding-left: 1rem;
}

.create .body {
    margin: 0;
}

.submit {
    display: flex;
    justify-content: flex-end;
}
  
.submit button {
  padding: 0.5rem 1rem;
}
@keyframes shake {
  50% {
    transform: translateX(10px);
  }
  100% {
    transform: translateX(-10px);
  }
}

@keyframes left-to-right {
  0% {
    transform: translateX(-5px);
    opacity: 0;
  }
}

.line {
  width: 100%;
  height: 2px;
  background: black;
  transform: scale(0, 1);
  transition: transform 500ms linear;
}

.forgot {
  text-align: center;
  margin-top: 1rem;
  font-size: 0.8rem;
}

.forgot a {
  text-decoration: none;
}

.forgot:hover a {
  text-decoration: underline;
}

.login-card {
  width: 360px;
  margin: auto;
  font-size: 1.5rem;
  padding: 2rem;
  border-radius: 5px;
  box-shadow: 3px 3px 3px #a3a3a357, -3px -3px 5px rgb(255 255 255 / 73%);
}

.login-card .logo {
  text-align: center;
  margin: 2rem;
}

.login-card span {
  margin-right: 0.5rem;
}

.login-card input {
  display: block;
  width: 100%;
  height: 2rem;
  border: none;
  margin-top: 1rem;
  border-radius: 5px;
  background: #e5e5e5;
}

.login-card input:focus + div {
  transform: scale(1);
  background-color: black;
}

.login-card form > div {
  margin-top: 2rem;
}

.login-card input:focus {
  outline: none;
  background: white;
}

.login-card label {
  color: rgb(102, 102, 102);
}

.login-card button {
  font-size: 1rem;
  padding: 0.5rem 0;
  margin-top: 2rem;
  width: 100%;
}

.login-card button:hover {
  cursor: pointer;
}

.not-found {
    display: flex;
    justify-content: space-around;
    align-items: flex-end;
    background: url("/static/media/404 Page Not Found _Monochromatic.9f0c5b0e.svg") no-repeat center;
    min-height: 360px;
    max-width: 480px;
    margin: auto;
}

.not-found p {
    font-family: system-ui;
    font-size: 2rem;
    color: #026379;
}

